import React, { Component } from 'react'
import './index.css'

export default class Search extends Component {
  handSeacrch(e){
    if(e.keyCode === 13&&e.target.value!==''){
      window.open('//www.baidu.com/s?wd=' + e.target.value)
    }
  }
  // componentWillMount(){
  //   fetch('https://free-api.heweather.net/s6/weather/forecast?location=shiyan&key=HE1903061702461886').then((res)=>{
  //     console.log(res)
  //   })
  // }
  render() {
    return (
      <div className='searchBox'>
        <input className='searchInput'onKeyDown={(e)=>{this.handSeacrch(e)}} type='text' placeholder='emmm'/>
        <div className="go"></div>
      </div>
    )
  }
}
